*{margin:0;padding:0;font-family:"Pretendard",Apple SD Gothic Neo,돋움,dotum,sans-serif;font-weight:400;font-size:inherit;word-break:break-all}html,body{width:100%;height:100%;color:#111}body{background-color:#fff}body{cursor:url("../images/mouse.png") 20 20,auto;padding:env(safe-area-inset-top, 20px) env(safe-area-inset-right, 20px) env(safe-area-inset-bottom, 20px) env(safe-area-inset-left, 20px)}h1,h2,h3,h4,p{margin:0}ul,ol,li,dl,dt,dd{list-style:none;margin:0;padding:0}button{padding:0;border:0;background-color:rgba(0,0,0,0);cursor:pointer}a{color:inherit;text-decoration:none}a:hover,button:hover,label:hover{cursor:url("../images/mouse_link.png") 20 20,auto}#wrapper{min-height:100vh;background-color:#fff}#container{position:relative;height:100%;min-height:100vh;padding:0;box-sizing:border-box;background-color:#fff}#header{position:fixed;display:flex;width:100%;padding:0 20px;box-sizing:border-box;align-items:center;justify-content:space-between;z-index:1}#header.white .header-logo{filter:brightness(0) invert(1);opacity:.5}#header.white .btn-music{color:#eee;border-color:#ddd;filter:brightness(0) invert(1);opacity:.8}#header .header-logo{mix-blend-mode:soft-light}#header .header-logo img{display:block;width:100%;height:50px}#header .btn-music{height:fit-content;padding:5px 10px;padding-right:30px;border:2px solid #111;border-radius:999px;background-repeat:no-repeat;background-size:auto 20px;background-position:center right 5px;font-size:14px;font-weight:700}#header .btn-music.play{background-image:url("../images/icon_pause.png")}#header .btn-music.pause{background-image:url("../images/icon_play.png")}.footer{background-color:#111}.footer-inner{padding:15px 20px;box-sizing:border-box}.footer p{font-size:13px;color:#fff;text-align:center}@media(max-width: 500px){#header .btn-music{width:24px;height:24px;padding:0;padding-right:0;border:0;background-size:contain;background-position:center center;text-indent:-99999px}}